.footer{
    background: black;
    width: 100%;
    padding-top: 40px;
    padding-bottom: .3125rem;
    color: #fff;
    position: absolute;
}


.footer-content{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}
.footer-content h3{
    font-size: 2.1rem;
    font-weight: 500;
    text-transform: capitalize;
    line-height: 3rem;
}
.footer-content p{
    max-width: 500px;
    margin: 10px auto;
    line-height: 28px;
    font-size: 14px;
    color: #cacdd2;
}
.socials{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.4rem 0 0.5rem 0;
}
.socials li{
    margin: 0 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.socials a{
    text-decoration: none;
    color: #fff;
    border: 1.1px solid white;
    padding: 5px;
    border-radius: 50%;

}
.socials a i{
    font-size: 1.1rem;
    width: 20px;
    transition: color .4s ease;

}
.socials a:hover i{
    color: aqua;
}

.footer-bottom{
    background: black;
    width: 100%;
    padding-inline: 20px;
    text-align: center;
}
.footer-bottom p{
    float:left;
    font-size: 14px;
    word-spacing: 2px;
    text-transform: capitalize;
}
.footer-bottom p a{
  color:#44bae8;
  font-size: 16px;
  text-decoration: none;
}
.footer-bottom span{
    text-transform: uppercase;
    opacity: .4;
    font-weight: 200;
}
.footer-menu{
    float: right;
    padding-bottom: .3125rem;
}
.footer-menu ul{
    display: flex;
}
.footer-menu ul li{
    padding-right: .625rem;
    display: block;
}
.footer-menu ul li a{
  color: #cfd2d6;
 
}
.footer-menu ul li a:hover{
  color: #27bcda;
}

@media (max-width:500px) {
.footer-menu ul{
  display: flex;
  margin-top: 10px;
  overflow:visible;
}

} 

